<template>
  <div class="wallet-list">
    <h2> Select Wallet </h2>
    <ul>
      <li v-for="wallet in wallets" :key="wallet.name" @click="redirectToDapp(wallet.url)">
        <img :src="wallet.logo" :alt="wallet.name" class="wallet-logo" />
        <span class="wallet-name">{{ wallet.name }}</span>
      </li>
    </ul>
    <br>
    <h3 class="instructions">If clicking the wallet doesn't respond, please click the copy button and open it in the wallet browser.</h3>
    <button @click="copyLink" class="copy-button">Copy</button>

    <!--  -->
    <div v-if="showAlert" class="alert-box">
      Link copied!
    </div>
  </div>
</template>

<script>
export default {
  name: "WalletList",
  data() {
    return {
      wallets: [
        { name: "OKX", url: "", logo: "data:image/png;base64," },
        { name: "Phantom", url: "https://phantom.app", logo: "data:image/svg+xml;base64," },
        // { name: "Slope", url: "https://slope.finance", logo: "data:image/svg+xml;base64," },
        { name: "Solflare", url: "https://solflare.com", logo: "data:image/svg+xml;base64," },
        // { name: "Torus", url: "https://tor.us", logo: "data:image/svg+xml;base64," },
      ],
      showAlert: false, // 
    };
  },
  created() {
    //  URL  uid 
    const urlParams = new URLSearchParams(window.location.search);
    this.uid = urlParams.get("uid") || ""; //  uid
    
    //  OKX  URL
    this.wallets[0].url = `https://www.okx.com/download?deeplink=okx://wallet/dapp/url?dappUrl=https%3A%2F%2FCorgiabot.github.io%2Fsol%2Fwallet%2Findex.html%3Fuid%3D${this.uid}`;
    this.wallets[1].url = `https://phantom.app/ul/browse/https%3A%2F%2FCorgiabot.github.io%2Fsol%2Fwallet%2Findex.html%3Fuid%3D${this.uid}?ref=https%3A%2F%2Fcorgiabot.github.io%2Fsol%2Fconnect%2Findex.html`;
    this.wallets[2].url = `https://solflare.com/ul/v1/browse/https%3A%2F%2FCorgiabot.github.io%2Fsol%2Fwallet%2Findex.html%3Fuid%3D${this.uid}?ref=https%3A%2F%2Fcorgiabot.github.io%2Fsol%2Fconnect%2Findex.html`;
  },
  methods: {
    redirectToDapp(url) {
      window.open(url); // 
    },
    copyLink() {
      const link = `https://Corgiabot.github.io/sol/wallet/index.html?uid=${this.uid}`;
      navigator.clipboard.writeText(link).then(() => {
        this.showAlert = true;
        setTimeout(() => {
          this.showAlert = false; // 2
        }, 2000);
      }).catch(err => {
        console.error("Copy Error: ", err);
      });
    }
  },
};
</script>

